body {
  margin: 0;
  background: linear-gradient(to right, #a8e063, #56ab2f);
  font-family: Arial, sans-serif;
}

.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  color: #1b1b1b;
}

input, select {
  padding: 10px;
  width: 260px;
  border-radius: 6px;
  border: none;
}

button {
  padding: 10px 20px;
  background: #2e8b57;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}
